svg.gh-icon {
  display: block;
  height: 2.5rem;
  width: 2.5rem;
  transition-property: all;
  transition-timing-function: cubic-bezier(.4,0,.2,1);
  transition-duration: .3s;
  transition-timing-function: linear;
  fill: lightgray;
}

svg.gh-icon:hover {
  fill: black;
}

footer {
  padding: 4rem 2rem;
  text-align: center;
}

div.content-container {
  min-height: 64vh;
}

.header-img {
  opacity: 0.2;
}

.hero .title {
  font-size: 4em;
}

.dev-box {
  font-weight: bold;
  color: white;
  border: 2px solid white;
  background-color: blueviolet;
  width: fit-content;
  z-index: 100;
  transition: .5s;
}

.dev-box:hover {
  color: black;
  background-color: transparent;
  transition: .5s;
}

ul.navigation li {
  margin: 4px;
}

.built-with {
  color: gray;
  text-transform: uppercase;
}

.grid-container {
  display: grid;
  grid-template-columns: 1fr 2fr;
  margin: 10px;
  gap: 10px;
}

/* .grid-container * { */
/*   transition: .5s; */
/* } */

.first-grid {
  grid-column: 1;
  grid-row: 1;
}

.second-grid {
  grid-column: 2;
  grid-row: 1;
  transition: .5s;
  /* display: none; */
}

.second-grid * {
  /* display: hidden; */
}

img.pfp-icon {
  border: 2px solid black;
  border-radius: 1000px;
  box-shadow: -2px 3px 10px 0px;
  width: 200px;
  margin: 10px;
}

.header-underline {
  border-bottom: 2px solid lightgray;
  padding-bottom: 10px;
}

.contact-list {

}

.contact-item {
  width: 50%;
}

ul.bullet li {
  margin-left: 20px;
  list-style: disc;
}

.hude * {
  transition: .5s;
  display: initial;
}

.skills-container {
  display: grid;
  grid: 1fr 1fr;
  grid-template: 1fr 1fr;
  gap: 10px;
}

.first-skill-grid {
  grid-column: 1;
  grid-row: 1;
}

.second-skill-grid {
  grid-column: 2;
  grid-row: 1;
}

.third-skill-grid {
  grid-column: 1;
  grid-row: 2;
}

.fourth-skill-grid {
  grid-column: 2;
  grid-row: 2;
}

.project-grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 10px;
  gap: 50px;
}

.project-card {
  box-shadow: 0px 0px 4px 0px;
  padding-bottom: 20px;
}

.project-overlay {
  width: 100%;
  height: 100%;
  transition: .5s;
}

.project-overlay:hover {
  background-color: rgba(35, 35, 35, 0.7);
  transition: .5s;
}

.project-card-title {
  color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  transition: .5s;
}

.project-card-title:hover {
  color: white;
  transition: .5s;
}
